@use '../util' as *;

.header {
    display: flex;
    flex-wrap: wrap;
    margin-top: rem(36);
    @include breakpoint(medium){
        display: flex;
        justify-content: space-between;
        align-items: centerwwwaw;
    }

    &__title {
        width: 100%;
        @include breakpoint(medium){
            justify-content: space-between;
            align-items: center;
        }
    }
    &__subtitle {
        font-size: rem(14);
        font-weight: 700;
        color: var(--Desaturated-Blue-Text);
    }

    &__toggle {
        margin-left: auto;
    }
}