html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--Very-Dark-Blue-BG);
  color: var(--White-Text);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  margin-bottom: 0.1875rem;
  font-size: 1.5rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --Lime-Green: hsl(163, 72%, 41%);
  --Bright-Red: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram-Start: hsl(37, 97%, 70%);
  --Instagram-End:hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --Toggle-bg-End: hsl(210, 78%, 56%);
  --Toggle-bg-Start: hsl(146, 68%, 55%);
  --Toggle: hsl(230, 22%, 74%);
  --Very-Dark-Blue-BG: hsl(230, 17%, 14%);
  --Very-Dark-Blue-Top-BG-Pattern: hsl(232, 19%, 15%);
  --Dark-Desaturated-Blue-Card-BG: hsl(228, 28%, 20%);
  --Desaturated-Blue-Text: hsl(228, 34%, 66%);
  --White-Text: hsl(0, 0%, 100%);
  --White-BG: hsl(0, 0%, 100%);
  --Very-Pale-Blue-Top-BG-Pattern: hsl(225, 100%, 98%);
  --Light-Grayish-Blue-Card-BG: hsl(227, 47%, 96%);
  --Dark-Grayish-Blue-Text: hsl(228, 12%, 44%);
  --Very-Dark-Blue-Text: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: centerwwwaw;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--Desaturated-Blue-Text);
}
.header__toggle {
  margin-left: auto;
}

.toggle {
  border: none;
  display: grid;
  gap: 0.3rem 0.75rem;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  margin: 0;
}
.toggle label {
  text-align: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
}
.toggle label[for=system] {
  grid-column: 2/3;
  grid-row: 2/3;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  height: 1.5rem;
  grid-column: 2/3;
  grid-row: 1/2;
  position: relative;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem;
  z-index: 1;
  position: relative;
  height: 1.5rem;
  width: 1.1875rem;
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--Toggle-bg-Start) 0%, var(--Toggle-bg-End) 98.02%);
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--Very-Dark-Blue-BG);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.card {
  padding: 1.25rem;
  background: var(--Dark-Desaturated-Blue-Card-BG);
  color: var(--Desaturated-Blue-Text);
  width: 15.9375rem;
  text-align: center;
  padding: 1.5625rem;
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1.75rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--White-Text);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-top: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__label {
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: 400;
  color: var(--Desaturated-Blue-Text);
  text-transform: uppercase;
}
.card__change {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--Lime-Green);
}
.card__change--down {
  color: var(--Bright-Red);
}